import React, { Component } from 'react'
import SubComponent from "./SubComponent"
export default class MainComponent extends Component<{cnt:number,text:(string|null)}> {
    render() {
        var cmpArray:Array<JSX.Element>=[];
        for (let i = 0; i < this.props.cnt; i++) {
          cmpArray.push(<SubComponent text={this.props.text==null?"":this.props.text}/>);
        }
    
        return (
            <div>
                {cmpArray}
            </div>
        )
    }
}
